* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;

}

img {
  display: block;
  max-width: 100%;
}


.all-btn {
  margin-top: 200px;
  text-align: center;


}

.btn-1 {

  height: 70px;
  width: 150px;
  border-radius: 70;

}

.btn-2 {
  height: 70px;
  width: 100px;
  margin-left: 50px;
  border-radius: 70;


}

.timer {
  margin: 50px;
  display: flex;

  flex-direction: row;

  justify-content: flex-start;

  padding: 50px;

}

.field {
  margin: auto
}